import React, {useEffect} from 'react';

/* Library */
import styled from 'styled-components';
import { useNavigate } from 'react-router-dom';

/* Styles */
import { Container } from '../../styles/Container';
import '../../styles/loading.scss'

/* Image */
import loadingImg from '../../assets/bios-img/windows98_logo_2.png'

const StyledLoading=styled.div`
    padding-top: 10px;
    width: 100vw;
    height: 100vh;
    position: relative;
    background: rgb(119,173,214);
`;
const LoadingImg = styled.img`
    width: 100vw;
    height: 100vh;
`
const LoadingAni = styled.div`
    position: absolute;
    left:0;
    bottom:0;
    width:100%;
    height:30px;
    background: 
      linear-gradient(
        0.25turn,
        transparent,
        #fff 45% 55%,
        transparent
      );
    animation: loading steps(16) 1s infinite;
    @keyframes loading {
      0% {
        background-position-x: -1012px;
      }
      100% {
        background-position-x: 1012px;
      }
    }
`

const Loading = () => {
  const navigate = useNavigate();
    useEffect(() => {
        const fakeLoadingtimer = setTimeout(() => {
            navigate('/windows')
            /* window.location.href='/windows' */
            console.log('Initial timeout!')
        }, 5000);
        return () => clearTimeout(fakeLoadingtimer);
      // eslint-disable-next-line react-hooks/exhaustive-deps
      }, []);

    return (
        <StyledLoading >
            <div className='glitch-bg glitch--style-1'>
              <LoadingImg className='glitch__img' src={loadingImg} alt="logo"/>
              <LoadingImg className='glitch__img' src={loadingImg} alt="logo"/>
              <LoadingImg className='glitch__img' src={loadingImg} alt="logo"/>
              <LoadingImg className='glitch__img' src={loadingImg} alt="logo"/>
              <LoadingImg className='glitch__img' src={loadingImg} alt="logo"/>
            </div>
            <LoadingAni/>
            <Container/>
        </StyledLoading>
    );
};

export default Loading;