import React, {useEffect} from 'react';
import {connect} from 'react-redux';
import { TutorialContentWrap, TutorialTitle, TutorialText, TutorialSubTitle, TutorialList, TutorialItem } from './StyledTutorialNotepad/StyledTutorialNotepad'

import WindowsFrame from './WindowsFrame/WindowsFrame'
import {focusNotepad, blurNotepad, minimizeNotepad, maximizeNotepad, disabledNotepad,exitNotepad} from '../../store/actions/actions'
import notepadImage from '../../assets/dockbar-icon/notepad.png'

function Notepad({ notepad, onNotepadFocus, onNotepadBlur, onNotepadMinimize, onNotepadMaximize, onNotepadDisabled, onNotepadExit, showTutorial}) {
    const inputRef = React.createRef();
  
    useEffect(() => {
      window.addEventListener('click', notepadBlur);
      inputRef.current.focus();
  
      return () => window.removeEventListener('click', notepadBlur);
      // eslint-disable-next-line
    }, []);
  
    function notepadBlur(event) {
      if (!document.querySelector('#Notepad').contains(event.target) &&
        !document.querySelector('#notepad-button').contains(event.target)) {
        onNotepadBlur();
      } else { onNotepadFocus(); }
    }
    
  
    const displayContent =
      notepad.show ?
        <WindowsFrame
          id="Notepad"
          x="120"
          y="90"
          width="600"
          /* height="348" */
          img={notepadImage}
          title="User Tutorial Manual - Notepad"
          blurred={notepad.blurred}
          showMenu={true}
          onMinimize={onNotepadMinimize}
          onMaximize={onNotepadMaximize}
          onDisabled={onNotepadDisabled}
          onExit={onNotepadExit}
          isMinimized={notepad.minimized}
          isMaximized={notepad.maximized}
          isDisabled={notepad.maximized}
          showTutorial={showTutorial}
          >
          <div
            className="Notepad"
            /* contentEditable="true" */
            ref={inputRef}
          >
            <TutorialContentWrap>
              <TutorialTitle>[] User Tutorial Manual</TutorialTitle>
              <TutorialText> <i>PORTFOLIO98</i>    . </TutorialText>
              <TutorialText><i>PORTFOLIO98</i>   ?</TutorialText>
              <br/>
              <hr/>
              <TutorialText style={{textAlign:'center',fontSize:'10px',marginBottom:'10px', marginTop:'10px'}}><i style={{fontSize:'10px',marginRight:'5px'}}>&#x274B;</i><i style={{color:'white',background:'red',padding:'2px 3px'}}>SKIP</i>    <i style={{padding:'2px 3px', background:'blue', color:'yellow'}}>User Tutorial</i><i style={{background:'rgb(195, 199, 203)',padding:'2px 3px'}}>X</i>   .<i style={{fontSize:'10px',marginLeft:'5px'}}>&#x274B;</i></TutorialText>
              <hr/>
              <TutorialSubTitle>&#x2776; </TutorialSubTitle>
              <br/>
              <TutorialList>
                  <TutorialItem><span>About :</span> <i>PORTFOLIO98</i> .</TutorialItem>
                  <TutorialItem><span>Velog :</span>    <i></i> .</TutorialItem>
                  <TutorialItem><span>Github :</span>    <i></i>  .</TutorialItem>
                  <TutorialItem><span>Project 01 :</span> <i> </i>  <i></i>   .</TutorialItem>
                  <TutorialItem><span>Project 02 :</span> <i></i>   <i></i>   . </TutorialItem>
                  <TutorialItem><span>Project 03 :</span> <i>AXIOS API </i>         . </TutorialItem>
                  <TutorialItem><span>ShutDown :</span>    <i>' '</i> .</TutorialItem>
                </TutorialList>
                <br/>
                <br/>
                <hr/>
                <br/>
              <TutorialSubTitle>&#x2777;  </TutorialSubTitle>
              <br/>
              <TutorialList>
                  <TutorialItem><span>(Resume) :</span>  <i></i>   .</TutorialItem>
                  <TutorialItem><span> :</span>  <i> </i> <i> </i> .</TutorialItem>
                  <TutorialItem><span> :</span>    <i></i> .</TutorialItem>
                  <TutorialItem><span> :</span>    <i></i>  .</TutorialItem>
                  <TutorialItem><span> :</span>    <i></i>. </TutorialItem>
                  <TutorialItem><span>(self-introduction) :</span>  <i></i> . </TutorialItem>
                </TutorialList>
                <br/>
                <br/>
                <hr/>
                <br/>
              <TutorialSubTitle>&#x2778;  </TutorialSubTitle>
              <br/>
              <TutorialList>
                  <TutorialItem><span>User Tutorial :</span>   <i>Tutorial</i>    .</TutorialItem>
                  <TutorialItem><span>(self-introduction) :</span>  <i></i> .</TutorialItem>
                  <TutorialItem><span>(Resume) :</span>  <i></i>   .</TutorialItem>
                  <TutorialItem><span>Project( ) :</span>  <i></i>   .</TutorialItem>
                  <TutorialItem><span>Contact :</span>  <i></i>   .</TutorialItem>
                </TutorialList>
                <br/>
                <br/>
                <hr/>
            </TutorialContentWrap>
          </div>
        </WindowsFrame> : null;
  
    return displayContent;
  }
  
  const mapStateToProps = (state) => {
    return {
      notepad: state.notepad,
      showTutorial: state.showTutorial,
    }
  }
  
  const mapDispatchToProps = (dispatch) => {
    return {
      onNotepadBlur: () => dispatch(blurNotepad()),
      onNotepadFocus: () => dispatch(focusNotepad()),
      onNotepadMinimize: () => dispatch(minimizeNotepad()),
      onNotepadMaximize: () => dispatch(maximizeNotepad()),
      onNotepadDisabled: () => dispatch(disabledNotepad()),
      onNotepadExit: () => dispatch(exitNotepad()),
    }
  }
  
  export default connect(mapStateToProps, mapDispatchToProps)(Notepad);