import React, {useEffect, useState/* , useMemo */} from 'react';

/* Library */
import styled from 'styled-components';

/* Styles */
/* import '../styles/Typing.css' */

const StyledBiosTyping = styled.span`
    white-space: pre-wrap;
    line-height: 1.4;
    animation: cursor 1s step-end infinite;
    animation-duration: 0.3s; 
    animation-iteration-count: 20;
    @keyframes cursor{
    0%{border-right: 5px solid #fff} 
    50%{border-right: 5px solid #000} 
    100%{border-right: 5px solid #fff} 
}
`

const BiosTyping = () => {
  const txt = `.    . \n      .\n    IT      , \n     .\n           .\n       .\n PORTFOLIO98\n  .`
  /* 'Hello World This is my portfolio space. \nBIOS(C)2023 JooSeHyeon OS, Inc\nASUS P4C800 ACPI BIOS Revision 1002\nCPU : Intel(R) Pentium(R) 4 CPU 2.80GHz \nSpeed : 2.82 GHz \nPress DEL to run Setup \nPress <F8> for BBS POPUP \nDDR Frequency 400 Mhz, Dual-Channel, Liner Mode \nChecking NVRAM.. \n \n512MB OK \nNuyhes Portfolio Setup OK......\n\n  \n . \n  Memory Checking ........\n'; */
  
  /*   $: > Booting systems...
  $: > Total memory found: 256kb
  $: > Scanning for bootable media     100%
  $: > No media found. Booting from network address 3E:40:9C:75
  $: > Loading BIOS 2.3                100%
  $: > Reticulating splines            100%
  $: > Booting console                 100%
  $:
  $: > Welcome to the retro console version 0.1a
  $:
  $: > Hello Designer023. How can I help you today?
  $: I can has matrix?
  $: > buffering matrix... 20%
  $: > buffering matrix... 66%
  $: > buffering matrix... 100%
  $: > So, would you like a red or blue pill? 
  ? (Y/N)  
  */
  
  const [Text, setText] = useState('');
  const [Count, setCount] = useState(0);
  
  useEffect(() => {
    let a=true;
    const interval = setInterval(() => {
        setText(Text + txt[Count]); //  set  +  
        setCount(Count + 1); //    
    }, 10);
    if(Count === txt.length)  {  // Count    Text.length  
        clearInterval(interval);//    setInterval 
        a=false;
        console.log(a);
        if(a===false){
          /*    useRef  input focus ..   */
        }
    }
    console.log(useEffect);
    return () => {clearInterval(interval)}; //  setInterval 
  })
  
  return (
  	<StyledBiosTyping className="text">{ Text }</StyledBiosTyping>
  )
}

export default BiosTyping;